import BackspaceOutlinedIcon from "@mui/icons-material/BackspaceOutlined";

import { useEffect, useState } from "react";
import { WORDS } from "./words";

const Wordle = () => {
  const [todayWord, setTodayWord] = useState("");
  const [finish, setFinish] = useState(false);
  const alert = [
    null,
    null,
    "Genius",
    "Magnificent",
    "Impressive",
    "Splendid",
    "Great",
    "Phew",
  ];
  const message = ["Not enough letters", "Not in word list"];
  const result = [];

  useEffect(() => {
    setTodayWord(WORDS[Math.floor(Math.random() * WORDS.length)].toUpperCase());
  }, []);

  //   useEffect(() => {
  //     if (lettersLeft === 0) reveal();
  //   });

  const [row, setRow] = useState("");
  const [guess, setGuess] = useState("");
  const [guesses, setGuesses] = useState([]);
  const [guess1, setGuess1] = useState("");
  const [guess2, setGuess2] = useState("");
  const [guess3, setGuess3] = useState("");
  const [guess4, setGuess4] = useState("");
  const [guess5, setGuess5] = useState("");
  const [guess6, setGuess6] = useState("");
  const [messageToShow, setMessageToShow] = useState("");
  const [failed, setFailed] = useState(false);
  const [currentRow, setCurrentRow] = useState(1);
  const [lettersLeft, setLettersLeft] = useState(5);
  const [shake, setShake] = useState(false);
  const [reset, setReset] = useState(false);
  const [notEnoughLetters, setNotEnoughLetters] = useState(false);
  const [row1, setRow1] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);
  const [row2, setRow2] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);
  const [row3, setRow3] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);
  const [row4, setRow4] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);
  const [row5, setRow5] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);
  const [row6, setRow6] = useState([
    "active",
    "active",
    "active",
    "active",
    "active",
  ]);

  //   console.log(guess);
  //   console.log(todayWord);

  // WHEN CLICKED ON LETTER OR ENTER
  const handleClick = (e) => {
    //IF THE PLAYER HAVEN'T ALREADY SOLVED THE GAME
    if (!finish) {
      //IF CLICKED ON A LETTER
      setNotEnoughLetters(false);
      if (e.target.innerHTML !== "ENTER") {
        if (lettersLeft !== 0) {
          setGuess(guess + e.target.innerHTML);
          setLettersLeft(lettersLeft - 1);
          //   console.log(guess);
        }
      }
      //IF CLIKED ON 'ENTER'
      else {
        //IF ALL LETTERS ARE POPULATED
        if (lettersLeft === 0) {
          setNotEnoughLetters(false);

          //IF THE CURRENT ROW IS 1-5
          if (currentRow <= 6) {
            //IF THE WORD IS IN THE WORD LIST
            if (WORDS.includes(guess)) {
              reveal(currentRow, guess);
              setLettersLeft(5);
              setCurrentRow(currentRow + 1);
              guesses.push(guess);
              setGuess("");
              setMessageToShow("");
            } else {
              // IF THE GUESS IS NOT IN THE WORD LIST
              setMessageToShow(message[1]);
            }
          }
          //IF THE PLAYER COULDN'T SOLVE THE GAME AFTER 6 GUESSES
          else {
            reveal(currentRow, guess);
            setFailed(true);
            setMessageToShow(todayWord);
          }
        }
        //IF NOT ALL LETTERS ARE POPULATED
        else {
          setNotEnoughLetters(true);
        }
      }
    }
  };

  const toggleClassName = () => {
    setTimeout(() => setReset(true), 6000);
  };

  //FUNCTION FOR REVEALING ANSWER
  function reveal(currentRow, guess) {
    for (let i = 0; i <= 4; i++) {
      if (guess.charAt(i) === todayWord.charAt(i)) result[i] = "green";
      else if (todayWord.includes(guess[i])) result[i] = "yellow";
      else result[i] = "inactive";
      document.getElementById(guess.charAt(i)).classList.add(result[i]);
    }
    switch (currentRow) {
      case 1:
        setRow1(result);
        break;
      case 2:
        setRow2(result);
        break;

      case 3:
        setRow3(result);
        break;

      case 4:
        setRow4(result);
        break;

      case 5:
        setRow5(result);
        break;

      case 6:
        setRow6(result);
        break;
      default:
        break;
    }
    if (todayWord === guess) {
      setFinish(true);
    }

    return result;
  }

  const deleteLetter = () => {
    if (lettersLeft !== 5) {
      setGuess(guess.slice(0, -1));
      setLettersLeft(lettersLeft + 1);
      //   console.log(guess);
      setMessageToShow("");
    }
  };

  return (
    <div className="wordle">
      {finish && <div className="message fade-out">{alert[currentRow]}</div>}
      {notEnoughLetters && (
        <div className="message fade-out">Not enough letters</div>
      )}
      {failed && <div className="message">{todayWord}</div>}
      {
        <div className={messageToShow !== "" ? `message fade-out` : `hide`}>
          {messageToShow}
        </div>
      }
      <div className="grid">
        <div className={`${shake && `row shake`} row`}>
          <div className={`box ${row1[0]}`}>
            {guess !== "" && currentRow === 1
              ? guess.charAt(0)
              : guesses[0] && guesses[0].charAt(0)}
          </div>
          <div className={`box ${row1[1]}`}>
            {guess !== "" && currentRow === 1
              ? guess.charAt(1)
              : guesses[0] && guesses[0].charAt(1)}
          </div>
          <div className={`box ${row1[2]}`}>
            {guess !== "" && currentRow === 1
              ? guess.charAt(2)
              : guesses[0] && guesses[0].charAt(2)}
          </div>
          <div className={`box ${row1[3]}`}>
            {guess !== "" && currentRow === 1
              ? guess.charAt(3)
              : guesses[0] && guesses[0].charAt(3)}
          </div>
          <div className={`box ${row1[4]}`}>
            {guess !== "" && currentRow === 1
              ? guess.charAt(4)
              : guesses[0] && guesses[0].charAt(4)}
          </div>
        </div>
        <div className="row">
          <div className={`box ${row2[0]}`}>
            {guess !== "" && currentRow === 2
              ? guess.charAt(0)
              : guesses[1] && guesses[1].charAt(0)}
          </div>
          <div className={`box ${row2[1]}`}>
            {guess !== "" && currentRow === 2
              ? guess.charAt(1)
              : guesses[1] && guesses[1].charAt(1)}
          </div>
          <div className={`box ${row2[2]}`}>
            {guess !== "" && currentRow === 2
              ? guess.charAt(2)
              : guesses[1] && guesses[1].charAt(2)}
          </div>
          <div className={`box ${row2[3]}`}>
            {guess !== "" && currentRow === 2
              ? guess.charAt(3)
              : guesses[1] && guesses[1].charAt(3)}
          </div>
          <div className={`box ${row2[4]}`}>
            {guess !== "" && currentRow === 2
              ? guess.charAt(4)
              : guesses[1] && guesses[1].charAt(4)}
          </div>
        </div>
        <div className="row">
          <div className={`box ${row3[0]}`}>
            {guess !== "" && currentRow === 3
              ? guess.charAt(0)
              : guesses[2] && guesses[2].charAt(0)}
          </div>
          <div className={`box ${row3[1]}`}>
            {guess !== "" && currentRow === 3
              ? guess.charAt(1)
              : guesses[2] && guesses[2].charAt(1)}
          </div>
          <div className={`box ${row3[2]}`}>
            {guess !== "" && currentRow === 3
              ? guess.charAt(2)
              : guesses[2] && guesses[2].charAt(2)}
          </div>
          <div className={`box ${row3[3]}`}>
            {guess !== "" && currentRow === 3
              ? guess.charAt(3)
              : guesses[2] && guesses[2].charAt(3)}
          </div>
          <div className={`box ${row3[4]}`}>
            {guess !== "" && currentRow === 3
              ? guess.charAt(4)
              : guesses[2] && guesses[2].charAt(4)}
          </div>
        </div>
        <div className="row">
          <div className={`box ${row4[0]}`}>
            {guess !== "" && currentRow === 4
              ? guess.charAt(0)
              : guesses[3] && guesses[3].charAt(0)}
          </div>
          <div className={`box ${row4[1]}`}>
            {guess !== "" && currentRow === 4
              ? guess.charAt(1)
              : guesses[3] && guesses[3].charAt(1)}
          </div>
          <div className={`box ${row4[2]}`}>
            {guess !== "" && currentRow === 4
              ? guess.charAt(2)
              : guesses[3] && guesses[3].charAt(2)}
          </div>
          <div className={`box ${row4[3]}`}>
            {guess !== "" && currentRow === 4
              ? guess.charAt(3)
              : guesses[3] && guesses[3].charAt(3)}
          </div>
          <div className={`box ${row4[4]}`}>
            {guess !== "" && currentRow === 4
              ? guess.charAt(4)
              : guesses[3] && guesses[3].charAt(4)}
          </div>
        </div>
        <div className="row">
          <div className={`box ${row5[0]}`}>
            {guess !== "" && currentRow === 5
              ? guess.charAt(0)
              : guesses[4] && guesses[4].charAt(0)}
          </div>
          <div className={`box ${row5[1]}`}>
            {guess !== "" && currentRow === 5
              ? guess.charAt(1)
              : guesses[4] && guesses[4].charAt(1)}
          </div>
          <div className={`box ${row5[2]}`}>
            {guess !== "" && currentRow === 5
              ? guess.charAt(2)
              : guesses[4] && guesses[4].charAt(2)}
          </div>
          <div className={`box ${row5[3]}`}>
            {guess !== "" && currentRow === 5
              ? guess.charAt(3)
              : guesses[4] && guesses[4].charAt(3)}
          </div>
          <div className={`box ${row5[4]}`}>
            {guess !== "" && currentRow === 5
              ? guess.charAt(4)
              : guesses[4] && guesses[4].charAt(4)}
          </div>
        </div>
        <div className="row">
          <div className={`box ${row6[0]}`}>
            {guess !== "" && currentRow === 6
              ? guess.charAt(0)
              : guesses[5] && guesses[5].charAt(0)}
          </div>
          <div className={`box ${row6[1]}`}>
            {guess !== "" && currentRow === 6
              ? guess.charAt(1)
              : guesses[5] && guesses[5].charAt(1)}
          </div>
          <div className={`box ${row6[2]}`}>
            {guess !== "" && currentRow === 6
              ? guess.charAt(2)
              : guesses[5] && guesses[5].charAt(2)}
          </div>
          <div className={`box ${row6[3]}`}>
            {guess !== "" && currentRow === 6
              ? guess.charAt(3)
              : guesses[5] && guesses[5].charAt(3)}
          </div>
          <div className={`box ${row6[4]}`}>
            {guess !== "" && currentRow === 6
              ? guess.charAt(4)
              : guesses[5] && guesses[5].charAt(4)}
          </div>
        </div>
      </div>
      <div className="keyboard">
        <div className={`row`}>
          <button className="letter" id="Q" onClick={handleClick}>
            Q
          </button>
          <button className="letter" id="W" onClick={handleClick}>
            W
          </button>
          <button className="letter" id="E" onClick={handleClick}>
            E
          </button>
          <button className="letter" id="R" onClick={handleClick}>
            R
          </button>
          <button className="letter" id="T" onClick={handleClick}>
            T
          </button>
          <button className="letter" id="Y" onClick={handleClick}>
            Y
          </button>
          <button className="letter" id="U" onClick={handleClick}>
            U
          </button>
          <button className="letter" id="I" onClick={handleClick}>
            I
          </button>
          <button className="letter" id="O" onClick={handleClick}>
            O
          </button>
          <button className="letter" id="P" onClick={handleClick}>
            P
          </button>
        </div>
        <div className={`row`}>
          <button className="letter" id="A" onClick={handleClick}>
            A
          </button>
          <button className="letter" id="S" onClick={handleClick}>
            S
          </button>
          <button className="letter" id="D" onClick={handleClick}>
            D
          </button>
          <button className="letter" id="F" onClick={handleClick}>
            F
          </button>
          <button className="letter" id="G" onClick={handleClick}>
            G
          </button>
          <button className="letter" id="H" onClick={handleClick}>
            H
          </button>
          <button className="letter" id="J" onClick={handleClick}>
            J
          </button>
          <button className="letter" id="K" onClick={handleClick}>
            K
          </button>
          <button className="letter" id="L" onClick={handleClick}>
            L
          </button>
        </div>
        <div className={`row`}>
          <button className="letter enter" onClick={handleClick}>
            ENTER
          </button>
          <button className="letter" id="Z" onClick={handleClick}>
            Z
          </button>
          <button className="letter" id="X" onClick={handleClick}>
            X
          </button>
          <button className="letter" id="C" onClick={handleClick}>
            C
          </button>
          <button className="letter" id="V" onClick={handleClick}>
            V
          </button>
          <button className="letter" id="B" onClick={handleClick}>
            B
          </button>
          <button className="letter" id="N" onClick={handleClick}>
            N
          </button>
          <button className="letter" id="M" onClick={handleClick}>
            M
          </button>
          <button className="letter backspace" onClick={deleteLetter}>
            <BackspaceOutlinedIcon />
          </button>
        </div>
      </div>
    </div>
  );
};

export default Wordle;
